import { request }  from '../../request.js' 
export default {
	data() {
		return {
			list: [
				{
					name: '充值'
				},
				{
					name: '提升等级'
				},
				{
					name: '我的银行卡'
				},
				{
					name: '提现'
				},
				{
					name: '收入明细'
				},
				{
					name: '提现列表'
				},
			],
			// 储存用户个人余额
			res:'',
			user_balance: '',
			$src: this.$src
		}
	},
	onLoad() {
		request({
			url: 'index.php?s=/wap/order/ApiorderInfo',
			method:"POST",
		}).then(res => {
			console.log(res)
			if(res.data) {
				this.res = res.data;
			}
		})
		this.getUserMoney();
	},
	methods:{
		// 获取用户余额信息
		getUserMoney() {
			request({
				url: 'index.php?s=/wap/member/getMomeyInfo'
			}).then(res => {
				
				this.user_balance = res.data.data;
				console.log(this.user_balance)
			})
		},
		// 跳转页面
		handleClick(index) {
			switch(index) {
				case 0:
				uni.navigateTo({
					url: '/pages/components/payUp/payUp'
				});
				break;
				case 1:
				uni.navigateTo({
					url:'/pages/components/payMoney/payMoney'
				})
				break;
				case 2:
				uni.navigateTo({
					url: '/pages/components/bank/bank'
				})
				break;
				case 3:
				uni.navigateTo({
					url: '/pages/components/withdraw/withdraw'
				})
				break;
				case 4:
				uni.navigateTo({
					url: '/pages/components/bill/bill'
				})
				break;
				case 5:
				uni.navigateTo({
					url: '/pages/components/withdraw-list/withdraw-list'
				})
				break;
			}
		}
	}
}
